*{
    margin: 0;
    box-sizing: border-box;
}

/* 1. Prepara el body para que el pseudo-elemento pueda posicionarse */
body {
  position: relative; /* Esencial para que el ::before se posicione absolutamente */
  margin: 0; /* Elimina márgenes por defecto del body */
  padding: 0;
  min-height: 100vh; /* Asegura que el body cubra toda la altura de la ventana */
}

/* 2. Crea la capa de fondo (simulando tu <div>) */
body::before {
  content: "";
  /* Posicionamiento y Tamaño (equivale a absolute top-0 h-screen w-screen) */
  position: absolute;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  /* Z-Index (equivale a z-[-2]) */
  z-index: -2;

  /* Fondo Base (equivale a bg-white) */
  background-color: white;

  /* Degradado Radial (equivale a bg-[radial-gradient(...)]) */
  background-image: radial-gradient(
    /* Forma y Posición: 100% 50% at 50% 0% */
    100% 50% at 50% 0%,
    /* Color inicial: Azul muy sutil (0.13 de opacidad) */
    rgba(0, 163, 255, 0.13) 0%,
    /* Se desvanece a transparente */
    rgba(0, 163, 255, 0) 50%,
    rgba(0, 163, 255, 0) 100%
  );
}